
 
  .barTitle {
    font-size: 1.3rem;
    margin-right: 20px;
  }
  .miniAlign {
    display: flex;
  }
  
  .miniAlign > div {
    margin-left: 6px;
  }
  h1 {
    color: white;
    font-size: 4rem !important;
  }
  
  .red {
    background: red;
  }
  .yellow {
    background: rgb(231, 178, 4);
    font-size: 2rem;
  }
  
  .green {
    background: rgb(1, 168, 1);
  }
  
  .mini {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;
    border-radius: 30px;
  }
  .cardkend {
      color: white;
    height: 20vw;
    background: #0c162d;
    filter: drop-shadow(5px 7px 5px #1c459c) drop-shadow(-10px -10px 15px #3156a7);
    overflow: hidden;
  }
  
  .bar {
    width: 100%;
    height: 35px;
    box-shadow: 5px 5px 10px 1px black;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
 
  .const {
    color: #0dbc79;
  }
  .require {
    color: tomato;
  }
  .var {
    color: rgb(204, 0, 255);
  }
 
  h2 {
    text-align: center;
    color: turquoise !important;
    margin-top: 10px;
    font-size: 1.7rem;
  }
  .infoCard {
    font-size: 1.2rem;
    margin-bottom: 15px;
  }
  @media only screen and (max-width: 768px) {
    /* For mobile phones: */
    h1 {
      font-size: 3rem !important;
    }
    .cardkend {
      font-size: 0.75rem;
      height: auto;
    }
    h2 {
      font-size: 1.4rem;
    }
    .heading {
      font-size: 1.1rem;
    }
  }

  