.box {
    background: ghostwhite;
    box-shadow: whitesmoke;
    filter: drop-shadow(5px 7px 5px #ff00ff) drop-shadow(-10px -10px 15px #ff00ff);
    transform: perspective(1500px) rotateY(-45deg);
    border-radius: 1rem;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
    transition: transform 1s ease 0s;
  }


  .center {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  button {
    width: 50%;
  }
  .paragraph,
  li {
    font-size: 2rem;
  }
  
  circle {
    width: 50px;
    height: 10px;
    background: white;
  }
  
  span {
    color: whitesmoke;
  }
  
 
  
  @media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .title {
      font-size: 3rem !important;
    }
    .paragraph,
    li {
      font-size: 1.5rem;
    }
   
  }