.inputControl {
  display: flex;
  flex-direction: column;
  width: 85%;
  margin-bottom: 30px;
}

input {
  height: 60px;
}
label {
  font-size: 1.4rem;
}
.notification {
  width: 85%;
  text-align: center;
  font-size: 1.4rem;
}
.sendButton {
  width: 85%;
}

.is-black {
  color: white !important;
  border: white solid 1px !important;
}

.is-black:hover {
  border: black solid 1px !important;
}

.infoButtons {
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: space-between;
  justify-content: space-evenly;
}
a {
  width: 50%;
}


@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  .inputControl {
    width: 92%;
  }
  .sendButton {
    width: 92%;
  }
  
  .title {
    font-size: 3rem !important;
  }
 
  .notification {
    width: 92%;
  }
}
