    width: 50px;
    height: 10px;
    background: white;
  }
  
  
  
  .imgwidth {
    margin-top: 3vw;
    width: 100%;
  }
  
  .card{
      transform:
      perspective(800px)
      rotateY(25deg) scale(0.9)
      rotateX(10deg);
    filter: blur(2px);
    opacity: 0.5;
    transition: 0.6s ease all;
  }
  
  .github{
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .card:hover {
      transform:
        perspective(800px)
        rotateY(-15deg)
        translateY(-50px)
        rotateX(10deg)
        scale(1);
      filter: blur(0);
      opacity: 1;
    }

    .move{
      margin-left: 7vw;
    }
  
    @media only screen and (max-width: 768px) {
    /* For mobile phones: */
   .title{
     font-size: 3rem !important;
   }
  
