  .cardB {
    margin-top: -2vw;;
    position: absolute;
    background: tomato;
  
    width: 25%;
    transform: scale(0.85) rotateY(-30deg) rotateX(45deg) translateZ(4.5rem);
    transform-origin: 50% 100%;
    transform-style: preserve-3d;
    box-shadow: 1rem 1rem 2rem rgba(0, 0, 0, 0.25);
    transition: 0.6s ease transform;
  }
  .is-two-fifths {
    margin-right: 50px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    
  }
 
  .card2 {
      margin-top: -2vw;;
    position: absolute;
    background: tomato;
  
    width: 25%;
    transform: scale(0.75) rotateY(-30deg) rotateX(45deg) translateZ(4.5rem) translateX(-6vw);
    transform-origin: 50% 100%;
    transform-style: preserve-3d;
    box-shadow: 1rem 1rem 2rem rgba(0, 0, 0, 0.25);
    transition: 0.6s ease transform;
  }
  
  .card3 {
    position: absolute;
    background: tomato;
    margin-top: -5vw;
    width: 25%;
    transform: scale(0.75) rotateY(-30deg) rotateX(45deg) translateZ(4.5rem)
      translateX(30vw) translateY(15vw);
    transform-origin: 50% 100%;
    transform-style: preserve-3d;
    box-shadow: 1rem 1rem 2rem rgba(0, 0, 0, 0.25);
    transition: 0.6s ease transform;
  }
  
  .cardB:hover {
    transform: scale(0.9);
  }
  
  .cardB::before {
    transform: translateZ(4rem);
  }
  
  .cardB::before:hover {
    transform: translateZ(0);
  }
  
  .cardB::after {
    transform: translateZ(-4rem);
  }
  
  .cardB::after:hover {
    transform: translateZ(-1px);
  }
  
  .paragraph {
    font-size: 2rem;
    text-align: justify;
  }
  
  .content {
    font-size: 2rem;
    width: 40%;
    position: absolute;
    top: 500%;
    left: 55%;
